import {norm, sqrt, abs, sum, round, random, pow} from 'mathjs'

export interface Now {
    temp: number;
    hum: number;
    press: number;
}

export function calc_score(now: Now): number{
    // データの加工　https://studyhacker.net/columns/best-temperature-efficiency
    var delta = [(now.temp-22.5)/5.5, (now.hum-55)/15*0.5, -(now.press-1013.25)/15*0.7];
    if(delta[0]<0){delta[0]=-delta[0]*0.7;} // temp: 寒い時は着込めばいいので暑い時より評価しない
    if(delta[1]<0){delta[1]=-delta[1];} // hum: 湿度は低い時はドライアイ，高い時も不快なので+-等しく評価
    if(delta[2]<0){delta[2]=0;} // press: 高気圧の時はイケイケなので考慮しない
    for(let i = 0; i <3 ; i++){delta[i] = delta[i]**1.1;}
    var index = sum(delta);
    var score = round(-100/3*index+100,3); // 100点満点で評価．
    return score;
}