import axios from 'axios';

export interface Body {
    OperationType : string;
    Ts_today : number;
    Ts_tom : number;
    API_KEY: string | undefined;
}

function createData(time: any, amount: any) {
    return { time, amount };
}

export function get_roomenv(body: Body): any{
    var url: string = process.env.REACT_APP_API_URL as string;
    axios.post(url, body)
      .then(res => {
        var temp=[], press=[], hum=[];
        var t, time
        if(res.data.Items){
          for (var i of res.data.Items) {
            t = i.Payload.date.split(" ")[1].split(":");
            time = t[0] + ':'+ t[1];
            temp.push(createData(time, i.Payload.temp));
            press.push(createData(time, i.Payload.press-1013.25));
            hum.push(createData(time, i.Payload.hum));
          }
        //   var dataset = {};
          var dataset = {
            temp: temp,
            press: press,
            hum: hum,
            temp_ave: res.data.means[0],
            temp_max: res.data.maxs[0],
            temp_min: res.data.mins[0],
            temp_var: res.data.vars[0],
            press_ave: res.data.means[1],
            press_max: res.data.maxs[1],
            press_min: res.data.mins[1],
            press_var: res.data.vars[1],
            hum_ave: res.data.means[2],
            hum_max: res.data.maxs[2],
            hum_min: res.data.mins[2],
            hum_var: res.data.vars[2],
            now: res.data.Items[res.data.Items.length - 1].Payload.date,
            temp_now: temp[temp.length-1].amount,
            press_now: press[press.length-1].amount+1013.25,
            hum_now: hum[hum.length-1].amount,
            loading: false
          };
          return dataset;
        }
      })
      .catch(function (error) {
        // handle error
        console.log('API request error occured: ' + error);
        return false;
      })
}