import React from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import Box from '@material-ui/core/Box';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Chart from './Chart';
import Deposits from './Deposits';
import axios from 'axios';
import { calc_score, Now } from '../utils/calc_score';
import { get_roomenv, Body } from '../utils/get_roomenv';

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  toolbar: {
    paddingRight: 24, // keep right padding when drawer closed
  },
  toolbarIcon: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: '0 8px',
    ...theme.mixins.toolbar,
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(['width', 'margin'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: 36,
  },
  menuButtonHidden: {
    display: 'none',
  },
  title: {
    flexGrow: 1,
  },
  drawerPaper: {
    position: 'relative',
    whiteSpace: 'nowrap',
    width: drawerWidth,
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  drawerPaperClose: {
    overflowX: 'hidden',
    transition: theme.transitions.create('width', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    width: theme.spacing(7),
    [theme.breakpoints.up('sm')]: {
      width: theme.spacing(9),
    },
  },
  appBarSpacer: theme.mixins.toolbar,
  content: {
    flexGrow: 1,
    height: '95vh',
    overflow: 'auto',
  },
  container: {
    paddingTop: theme.spacing(4),
    paddingBottom: theme.spacing(4),
  },
  paper: {
    padding: theme.spacing(2),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column',
  },
  fixedHeight: {
    height: 240,
  },
}));

function createData(time: any, amount: any) {
  return { time, amount };
}

function createNow(temp: number, press: number, hum: number){
  var now: Now;
  now = {temp: temp, press: press, hum: hum};
  return now;
}

var dammy = [
  {time: '00:00', amount: null},
  {time: '04:00', amount: null},
  {time: '08:00', amount: null},
  {time: '12:00', amount: null},
  {time: '16:00', amount: null},
  {time: '20:00', amount: null},
  {time: '24:00', amount: null},
];

export default function Dashboard(props: any) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(true);
  const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);

  // from api
  const [data, setData] = React.useState(
    {
      temp: dammy,
      press: dammy,
      hum: dammy,
      score: dammy,
      temp_ave: 0.00,
      temp_max: 0.00,
      temp_min: 0.00,
      temp_var: 0.00,
      press_ave: 0.00,
      press_max: 0.00,
      press_min: 0.00,
      press_var: 0.00,
      hum_ave: 0.00,
      hum_max: 0.00,
      hum_min: 0.00,
      hum_var: 0.00,
      now: 'date',
      temp_now: 0.00,
      press_now: 0.00,
      hum_now: 0.00,
      score_now: 0.00,
      loading: true
    }
  );

  React.useEffect(() => {
    var d = new Date();
    var year  = d.getFullYear();
    var month = d.getMonth() + 1;
    var day   = d.getDate();
    var hour  = ( d.getHours()   < 10 ) ? '0' + d.getHours()   : d.getHours();
    var min   = ( d.getMinutes() < 10 ) ? '0' + d.getMinutes() : d.getMinutes();
    // var sec   = ( d.getSeconds() < 10 ) ? '0' + d.getSeconds() : d.getSeconds();
    var ts = year + '-' + month + '-' + day + ' ' + hour + ':' + min + ':' + '00';
    const ts_today = Date.parse( ts.replace( /-/g, '/') ) / 1;

    const body: Body = {
        "OperationType":"TERM_SCAN",
        "Ts_today": ts_today - 86400000,
        "Ts_tom": ts_today,
        "API_KEY": process.env.REACT_APP_API_KEY
    }
    var sss = +min;
    console.log(sss);
    if (sss%15 === 0 || open == true){
      var url: string = process.env.REACT_APP_API_URL as string;
      var valiu = get_roomenv(body);
      setOpen(false);
      axios.post(url, body)
        .then(res => {
          var temp=[], press=[], hum=[], score = [];
          var t, time, result;
          if(res.data.Items){
            for (var i of res.data.Items) {
              t = i.Payload.date.split(" ")[1].split(":");
              time = t[0] + ':'+ t[1];
              temp.push(createData(time, i.Payload.temp));
              press.push(createData(time, i.Payload.press-1013.25));
              hum.push(createData(time, i.Payload.hum));
              result = calc_score(createNow(i.Payload.temp, i.Payload.press, i.Payload.hum));
              score.push(createData(time, result));
            }
            setData({
              temp: temp,
              press: press,
              hum: hum,
              score: score,
              temp_ave: res.data.means[0],
              temp_max: res.data.maxs[0],
              temp_min: res.data.mins[0],
              temp_var: res.data.vars[0],
              press_ave: res.data.means[1],
              press_max: res.data.maxs[1],
              press_min: res.data.mins[1],
              press_var: res.data.vars[1],
              hum_ave: res.data.means[2],
              hum_max: res.data.maxs[2],
              hum_min: res.data.mins[2],
              hum_var: res.data.vars[2],
              now: res.data.Items[res.data.Items.length - 1].Payload.date,
              temp_now: temp[temp.length-1].amount,
              press_now: press[press.length-1].amount+1013.25,
              hum_now: hum[hum.length-1].amount,
              score_now: score[score.length-1].amount,
              loading: false
            });
        }
      })
      .catch(function (error) {
        // handle error
        console.log('API request error occured: ' + error);
      })
    }
  });

  return (
    <div className={classes.root}>
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" className={classes.container}>
          <Grid container spacing={1}>
            {/* room conditions */}
            <Grid item xs={12} md={8} lg={9}>
              <Paper className={fixedHeightPaper}>
                <Chart data={data.score} title='Room conditions' />
              </Paper>
            </Grid>
            {/* Recent room conditions */}
            <Grid item xs={12} md={8} lg={3}>
              <Paper className={fixedHeightPaper}>
                <Deposits loading={data.loading} val_now={data.score_now} val_ave={data.temp_ave} val_max={data.temp_max} 
                val_min={data.temp_min} val_var={data.temp_var} now={data.now} val='Room Conditions' unit='-' />
              </Paper>
            </Grid>
            {/* temparature */}
            <Grid item xs={12} md={8} lg={9}>
              <Paper className={fixedHeightPaper}>
                <Chart data={data.temp} title='Temparature [C]' />
              </Paper>
            </Grid>
            {/* Recent temp. */}
            <Grid item xs={12} md={8} lg={3}>
              <Paper className={fixedHeightPaper}>
                <Deposits loading={data.loading} val_now={data.temp_now} val_ave={data.temp_ave} val_max={data.temp_max} 
                val_min={data.temp_min} val_var={data.temp_var} now={data.now} val='Temparature' unit='C' />
              </Paper>
            </Grid>
            {/* pressure */}
            <Grid item xs={12} md={8} lg={9}>
              <Paper className={fixedHeightPaper}>
                <Chart data={data.press} title='Delta Pressure [hPa]' />
              </Paper>
            </Grid>
            {/* Recent press. */}
            <Grid item xs={12} md={8} lg={3}>
              <Paper className={fixedHeightPaper}>
                <Deposits loading={data.loading} val_now={data.press_now} val_ave={data.press_ave} val_max={data.press_max} 
                val_min={data.press_min} val_var={data.press_var} now={data.now} val='Pressure' unit='hPa' />
              </Paper>
            </Grid>
            {/* moisuture */}
            <Grid item xs={12} md={8} lg={9}>
              <Paper className={fixedHeightPaper}>
                <Chart data={data.hum} title='Moisture [%]' />
              </Paper>
            </Grid>
            {/* Recent moisture */}
            <Grid item xs={12} md={8} lg={3}>
              <Paper className={fixedHeightPaper}>
                <Deposits loading={data.loading} val_now={data.hum_now} val_ave={data.hum_ave} val_max={data.hum_max} 
                val_min={data.hum_min} val_var={data.hum_var} now={data.now} val='Moisture' unit='%' />
              </Paper>
            </Grid>
            {/* Recent Orders */}
            {/* <Grid item xs={12}>
              <Paper className={classes.paper}>
                <Orders />
              </Paper>
            </Grid> */}
          </Grid>
          <Box pt={4}>
          </Box>
        </Container>
      </main>
    </div>
  );
}
