function ToDoList(props){
  var i = 0;
  return (
    <ul>
      {props.Item.map(item => (
        <li value={i++}>{item}</li>
      ))}
    </ul>
  );
}
var rands = []
export default function Roulette(props) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(true);
  const handleDrawerOpen = () => {
    setOpen(true);
  };
  const handleDrawerClose = () => {
    setOpen(false);
  };
  const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);

  const toDo = [
    'Study CCNA', 'Study LPIC', 
    'Coding Noise simulation', 'Coding IoT system',
    'Read a Novel', 'Study NLP in fast.ai',
  ]

  React.useEffect(() => {
  });
  const [determin_todo, detToDo] = React.useState('');
  const [loading, load] = React.useState(false);
  const [time, setTime] = React.useState({time: null, preset: [15, 30, 45, 60, 90]});
  function election(e){
    console.log('Election Now!');
    load(true);
    const id = toDo.length;
    var d = new Date();
    var year  = d.getFullYear();
    var month = d.getMonth() + 1;
    var day   = d.getDate();
    var hour  = ( d.getHours()   < 10 ) ? '0' + d.getHours()   : d.getHours();
    var min   = ( d.getMinutes() < 10 ) ? '0' + d.getMinutes() : d.getMinutes();
    // var sec   = ( d.getSeconds() < 10 ) ? '0' + d.getSeconds() : d.getSeconds();
    var ts = year + '-' + month + '-' + day + ' ' + hour + ':' + min + ':' + '00';
    const ts_today = Date.parse( ts.replace( /-/g, '/') ) / 1;
  
    const body = {
        "OperationType":"TERM_SCAN",
        "Ts_today": ts_today - 86400000,
        "Ts_tom": ts_today,
        "API_KEY": "mylifetour"
    }
    axios.post(`https://r0l90z3a61.execute-api.us-east-2.amazonaws.com/BME280/bme280get`, body)
        .then(res => {
          var now = res.data.Items[res.data.Items.length-1].Payload
          var rand = null
          var loop_flag = false;
          if(rands.length == id){
            console.log('一通りしました．');
            loop_flag = true
            rands = []
          }
          while(true){
            rand = Math.floor( Math.random() * (id));
            console.log('rand : ' + rand);
            if(!rands.includes(rand)){
              rands.push(rand);
              console.log(rands);
              console.log(toDo[rand]);
              load(false);
              var times = Math.sqrt((Math.pow(now.temp-22, 2)+ Math.pow(now.hum-50, 2) + Math.pow(now.press-1013.25,2)))// /(Math.pow(now.temp,2)+Math.pow(now.hum,2)+Math.pow(now.press,2)));
              console.log(times);
              if(loop_flag){
                detToDo(toDo[rand] + '  (refleshed)');
              }else{detToDo(toDo[rand])}
              return toDo[rand]
            }
          }
        })
        .catch(function (error) {
          // handle error
          console.log('API request error occured: ' + error);
          return 'error today :('
        })
  }

  return (
    <div className={classes.root}>
      <main className={classes.content}>
        <div className={classes.appBarSpacer} />
        <Container maxWidth="lg" className={classes.container}>
        
          <paper></paper>
        </Container>
      </main>
    </div>
  );
}

