import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { CountdownCircleTimer } from "react-countdown-circle-timer";
import Typography from '@material-ui/core/Typography';
// import classes from '*.module.css';

const minuteSeconds = 60;
const hourSeconds = 3600;
const daySeconds = 86400;

const useStyles = makeStyles((theme) => ({
  innerTimer: {
    textAlign: 'center'
  }
}));

const renderTime = ( remainingTime, todo , score) => {
  const classes = useStyles;
  console.log(classes.innerTimer);
  if (remainingTime === 0) {
    return <div className="timer">Well Done.</div>;
  }
  return (
    <div className="timer">
    <div className={classes.innerTimer}>
      <Typography className="textPirmary" variant='h4'>{todo}</Typography>
      <Typography className="textPirmary">{score}</Typography>
      <Typography className="textPirmary">{remainingTime}</Typography>
    </div>
    </div>
  );
};

