function alignTimes(time){
  return ( '0' + time ).slice( -2 );
};

const getTimeSeconds = (time) => (minuteSeconds - time) | 0;
const getTimeMinutes = (time) => ((time % hourSeconds) / minuteSeconds) | 0;
const getTimeHours = (time) => ((time % daySeconds) / hourSeconds) | 0;
const getTimeDays = (time) => (time / daySeconds) | 0;
const getTimes = (time) => {
  var hours = Math.floor(time/hourSeconds);
  var minutes = Math.floor((time%hourSeconds)/minuteSeconds);
  var seconds = Math.floor(time%hourSeconds%minuteSeconds);
  return alignTimes(hours) + ':' + alignTimes(minutes) + ':' + alignTimes(seconds )
}

export default function TodoTimer(props){
  return(
    <div>
    <CountdownCircleTimer
      isPlaying
      duration={props.time}
      size={240}
      colors={[["#004777", 0.33], ["#F7B801", 0.33], ["#A30000", 0.33]]}
      onComplete={() => [false, 1000]}
    >
    {({ elapsedTime }) =>
            renderTime(getTimes(props.time - elapsedTime), props.todo, props.score) //renderTime(getTimeMinutes(hourSeconds - elapsedTime), getTimeSeconds(elapsedTime), props.todo, props.score)
    }
    </CountdownCircleTimer>
    </div>
    )
}
